import React from 'react';




const HourlyWeather = (props) => {

  function HourlyWeatherList(props) {
    if (props.hourly.hourly.length > 0) {
      const hourly = props.hourly.hourly;
      const hourlyItems = hourly.map((hourlyItem, index) =>
        <li className="hourly-item" key={index}>
          <span className="hourly-time">{props.hourly.timeConverter(hourlyItem.time) + ':00'}</span>
          <i className={'icon-weather ' + hourlyItem.icon}></i>
          <span className="hourly-temperature">{props.hourly.tempConverter(hourlyItem.temperature)}&#8451;</span>
        </li>
      );
      return (
        <ul className="hourly-container">{hourlyItems}</ul>
      );
    } else {
      return (
        <h1>Loading Content...</h1>
      );
    }
   
  }
  

  return (
    <section className="hourly-weather">
    
      <HourlyWeatherList hourly={props} />
        
      

      
    </section>
  );
}

export default HourlyWeather;


