import React, { Component } from 'react';

import Hamburger from './Hamburger'
var classNames = require('classnames');

class Nav extends Component {
  constructor(props) {
    super(props);

    this.state = {
      navIsOpenState: false
    }

    this.toggleNav = this.toggleNav.bind(this);
    
  }
  toggleNav() {
    this.setState({
      navIsOpenState: !this.state.navIsOpenState
    }, () => {
      console.log(this.state.navIsOpenState)
    })
  }
  render() {
    let navIsOpen = classNames({
      'nav-is-open': this.state.navIsOpenState
    });
    let overlayVisible = classNames({
      'visible': this.state.navIsOpenState
    });
    return (
      <section>
        <div className={'overlay ' + overlayVisible} onClick={this.toggleNav}></div>
        <div className={'nav-wrap ' + navIsOpen}>
          <nav >
            <ul className="nav-container">
              <li>
                <a href="https://github.com/MaxColesFrederiksen" target="_blank" rel="noopener noreferrer" className="contact-link">
                  <i href="#" className="icon github"></i>
                  <span className="contact-span">github.com/MaxColesFrederiksen</span> 
                </a> 
                
              </li>
    
              <li>
                <a href="https://linkedin.com/in/maxcolesfrederiksen" target="_blank" rel="noopener noreferrer" className="contact-link">
                  <i href="#" className="icon linkedin"></i>
                  <span className="contact-span">linkedin.com/in/maxcolesfrederiksen</span>  
                </a>
              </li>
    
              <li>
                <a href="mailto:max@colesmedia.se" target="_blank" rel="noopener noreferrer" className="contact-link">
                  <i href="#" className="icon email"></i>
                  <span className="contact-span">max@colesmedia.se</span> 
                </a>
              </li>
    
              <li>
                <a href="https://colesmedia.se" target="_blank" rel="noopener noreferrer" className="contact-link">
                  <i href="#" className="icon website"></i>
                  <span className="contact-span">https://colesmedia.se</span>
                </a>  
              </li>
            </ul>
    
            <a href="#" className="close" onClick={this.toggleNav}>Close</a>
          </nav>
        </div>
        <Hamburger onClick={this.toggleNav}/>
      </section>
    );
  }
}

export default Nav;


