import React from 'react';

import Nav from './Nav';

const Hero = (props) => {

    return (
      <section className="hero">
        
        <div className="hero-container">
          <Nav />
        
          <i className="icon hero-weather-icon"></i>
  
          <h2 className="location">{props.city}, {props.country}</h2>
          <h1 className="temperature">{props.tempConverter(props.currently.temp)}&#8451;</h1>
          <span className="date">{props.dateConverter(props.currently.time)}</span>
          </div>
      </section>
    );
  
}

export default Hero;


