import React from 'react';



const Hamburger = (props) => {
  return (
    <section>
      <a className="hamburger-wrap" onClick={props.onClick}>
        <div className="top"></div>
        <div className="middle"></div>
        <div className="bottom"></div>
      </a>
    </section>
  );
}

export default Hamburger;


