import React, { Component } from 'react';

import './css/normalize.css';

import './css/variables.scss'
import './css/main.scss'

import Hero from './components/Hero';
import HourlyWeather from './components/HourlyWeather';
import DailyWeather from './components/DailyWeather';

import moment from 'moment';


class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      city: null,
      country: null,
      currently: {
        temp: null,
        wind: null,
        time: null,
        icon: null
      },
      hourly: [],
      daily: [],
      
    };
  }

  dateConverter(time) {
    return moment.unix(time).format("MMMM Do YYYY");
  }

  dayConverter(time) {
    return moment.unix(time).format('dddd');
  }

  timeConverter(time) {
    return moment.unix(time).hour();
  }

  tempConverter(temp) {
    return Math.floor((temp - 32) * 5/9)
  }

  windConverter(wind) {
    return Math.floor(wind / 0.621 * 1000 / 60 / 60)
  }

  componentDidMount() {
    fetch('https://api.ipdata.co?api-key=4f59b53c62a08acdea1c23dd21fb21292dfc78a4e21eb7a94db0a2f2')
      .then((res) => {return res.json()})
      .then((data) => {
        let lat = data.latitude;
        let lng = data.longitude;
        console.log(data)
        this.setState({
          city: data.city,
          country: data.country_name
        })
        
        fetch(`https://cors-anywhere.herokuapp.com/https://api.darksky.net/forecast/fee742f15ed33d39238a97ad58399df1/${lat},${lng}`)
        .then((res) => {return res.json()})
        .then((data) => {
          console.log(data)

          // what we need
          // 1. Temperature in celsius
          // 2. Wind // {mph / 0.621 * 1000 / 60 / 60}
          // 4. date & time //var dateString = moment.unix(value).format("MM/DD/YYYY");
          // 5. Icons based on the "icon" property in the response

          this.setState({
            currently: {
              temp: data.currently.temperature,
              wind: data.currently.windSpeed,
              time: data.currently.time,
              icon: data.currently.icon
            },
            hourly: data.hourly.data,
            daily: data.daily.data
          }, () => {
            console.log(this.state);
          })
        })
        .catch((err) =>{
          console.log(err)
        })
      })
      .catch((err) => {
        console.log(err);
      })
  }
  render() {
    return (
      <div className="App">
        <Hero 
          timeConverter={this.timeConverter.bind(this)}
          tempConverter={this.tempConverter.bind(this)} 
          dateConverter={this.dateConverter.bind(this)}
          currently={this.state.currently}
          city={this.state.city}
          country={this.state.country}
          />
        <HourlyWeather 
          tempConverter={this.tempConverter.bind(this)}
          timeConverter={this.timeConverter.bind(this)}
          hourly={this.state.hourly}
        />
        <DailyWeather 
          tempConverter={this.tempConverter.bind(this)}
          dayConverter={this.dayConverter.bind(this)}
          windConverter={this.windConverter.bind(this)}
          daily={this.state.daily}
        
        />

      </div>
    );
  }
}

export default App;



