import React from 'react';

const DailyWeather = (props) => {

  function DailyWeatherList(props) {
    if (props.daily.daily.length > 0) {
      const daily = props.daily.daily;
      const hourlyItems = daily.map((dailyItem, index) =>
        <li className="daily-item" key={index}>
          <span className="daily-day">{props.daily.dayConverter(dailyItem.time)}</span>
          <i className={'icon-weather ' + dailyItem.icon}></i>
          <span className="daily-temperature">{props.daily.tempConverter(dailyItem.temperatureHigh)}&#8451;</span>
          <span className="daily-wind">{props.daily.windConverter(dailyItem.windSpeed)} m/s</span>
          <i className="icon icon-wind"></i>
        </li>
      );
      return (
        <ul className="daily-container">{hourlyItems}</ul>
      );
    } else {
      return (
        <h1>Loading Content...</h1>
      );
    }
   
  }
  

  return (
    <section className="daily-weather">
    
      <DailyWeatherList daily={props} />
        
    </section>
  );
}

export default DailyWeather;


